<template>
  <div class="hello" @click="toggleActive">
    <ml-i-view v-model="imgShow" url="../assets/1.jpeg" :scale="3"></ml-i-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      url: '../assets/1.jpeg',
      imgShow: true
    }
  },
  methods: {
    toggleActive() {
      this.imgShow = !this.imgShow
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 400px;
  height: 400px;
}

</style>



